<template>
  <div class="list-wrap">
    <p class="title">
      项目投标收费情况
      <a class="el-icon-arrow-right morebtn" href="javascript:;" @click="openDown()"></a>
    </p>
    <div v-if="closeUp">
      <el-form size="small" :model="chargeForm" label-width="110px" ref="chargeForm">
        <div class="flexbox">
          <el-form-item label="项目产值：" prop="price" class="flexitem">
            <el-input
              :readonly="isRead"
              v-model.number="chargeForm.price"
              auto-complete="off"
              placeholder="请填写项目产值"
            >
              <template slot="append">元</template>
            </el-input>
          </el-form-item>
          <el-form-item label="资质使用费用：" prop="price" class="flexitem">
            <el-input
              :readonly="isRead"
              v-model.number="chargeForm.price"
              auto-complete="off"
              placeholder="请填写资质使用费用"
            >
              <template slot="append">元</template>
            </el-input>
          </el-form-item>
        </div>
        <div class="flexbox">
          <el-form-item label="标书制作费：" prop="price" class="flexitem">
            <el-input
              :readonly="isRead"
              v-model.number="chargeForm.price"
              auto-complete="off"
              placeholder="请填写标书制作费"
            >
              <template slot="append">元</template>
            </el-input>
          </el-form-item>
          <el-form-item label="技术服务费：" prop="price" class="flexitem">
            <el-input
              :readonly="isRead"
              v-model.number="chargeForm.price"
              auto-complete="off"
              placeholder="请填写技术服务费"
            >
              <template slot="append">元</template>
            </el-input>
          </el-form-item>
        </div>
        <el-form-item label="出现场费：" prop="price" class="sigle-ww">
          <el-input
            :readonly="isRead"
            v-model.number="chargeForm.price"
            auto-complete="off"
            placeholder="请填写出现场费"
          >
            <template slot="append">元</template>
          </el-input>
        </el-form-item>
        <div class="bid-subtotal">
          投标收费小计：
          元
        </div>
      </el-form>
    </div>

    <!-- 添加、编辑 -->
    <el-dialog
      :title="chargeTitle"
      :visible.sync="chargeFormVisible"
      width="640px"
      v-dialogDrag
      append-to-body
    >
      <el-form size="small" :model="chargeForm" label-width="110px" ref="chargeForm">
        <el-form-item
          label="单位全称："
          prop="companyname"
          :rules="[{ required: true, message: '请填写单位全称', trigger: 'change' }]"
        >
          <el-input
            :readonly="isRead"
            v-model="chargeForm.companyname"
            auto-complete="off"
            placeholder="请填写单位全称"
          ></el-input>
        </el-form-item>
        <div class="flexbox">
          <el-form-item
            label="单位联系人："
            prop="name"
            class="flexitem"
            :rules="[{ required: true, message: '请填写单位联系人', trigger: 'change' }]"
          >
            <el-input
              :readonly="isRead"
              v-model="chargeForm.name"
              auto-complete="off"
              placeholder="请填写单位联系人"
            ></el-input>
          </el-form-item>
          <el-form-item
            label="联系人电话："
            prop="phone"
            class="flexitem"
            :rules="[{ required: true, message: '请填写联系人电话', trigger: 'change' }]"
          >
            <el-input
              :readonly="isRead"
              v-model="chargeForm.phone"
              auto-complete="off"
              placeholder="请填写联系人电话"
            ></el-input>
          </el-form-item>
        </div>
        <div class="flexbox">
          <el-form-item label="单位负责人：" prop="name" class="flexitem">
            <el-input
              :readonly="isRead"
              v-model="chargeForm.name"
              auto-complete="off"
              placeholder="请填写单位负责人"
            ></el-input>
          </el-form-item>
          <el-form-item label="负责人电话：" prop="phone" class="flexitem">
            <el-input
              :readonly="isRead"
              v-model="chargeForm.phone"
              auto-complete="off"
              placeholder="请填写负责人电话"
            ></el-input>
          </el-form-item>
        </div>
        <el-form-item label="详细情况描述：" prop="remark">
          <el-input
            type="textarea"
            :readonly="isRead"
            :autosize="{ minRows: 3}"
            v-model="chargeForm.remark"
            placeholder="详细情况描述"
          ></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer">
        <el-button size="small" @click="chargeFormVisible=false">取消</el-button>
        <el-button size="small" type="primary" @click="submitchargeForm()">保存</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "chargeSituation",
  components: {},
  props: { chargeForm: Object },
  data() {
    return {
      chargeTitle: "",
      isType: "",
      isRead: false,
      chargeFormVisible: false,
      closeUp:false,
    };
  },
  created() {},
  watch: {
    // partAlist: function(newValue, oldValue) {
    //   this.$emit("update:partAlist", newValue);
    // }
  },
  methods: {
    //   展开 
    openDown(){
        this.closeUp = true;
    },
    //   新增列表数据
    addInfoList() {
      this.chargeTitle = "添加收款记录";
      this.chargeFormVisible = true;
      this.isType = "add";
      this.isRead = false;
      this.chargeForm = {};
    },
    // 详情
    infoDetail(index) {
      this.chargeTitle = "收款记录详情";
      this.isRead = true;
      this.chargeForm = Object.assign({}, this.infoList[index]);
      this.chargeFormVisible = true;
      this.isType = "detail";
    },
    // 编辑
    infoEdit(index) {
      this.chargeTitle = "编辑收款记录";
      this.chargeFormVisible = true;
      this.isType = "edit";
      this.isRead = false;
      this.chargeForm = Object.assign({}, this.infoList[index]);
    },
    // 删除
    infoDelete(index) {
      this.infoList.splice(index, 1);
    },
    // 表单提交
    submitchargeForm() {
      this.$refs.chargeForm.validate(valid => {
        if (valid) {
          if (this.isType == "add") {
            this.addInfoSubmit();
          } else if (this.isType == "edit") {
            this.editInfoSubmit();
          } else {
            this.chargeFormVisible = false;
          }
        } else {
          return false;
        }
      });
    },
    // 添加提交
    addInfoSubmit() {
      let obj = Object.assign({}, this.chargeForm);
      this.infoList.push(obj);
      this.chargeFormVisible = false;
    },
    // 编辑提交
    editInfoSubmit(index) {
      let obj = Object.assign({}, this.chargeForm);
      this.infoList.splice(this.index, 1, obj);
      this.chargeFormVisible = false;
    }
  }
};
</script>

<style lang="scss" scoped>
.list-wrap {
  .title {
    color: #0099ff;
    font-size: 14px;
    line-height: 20px;
    border-left: 3px solid #0099ff;
    padding-left: 6px;
    margin-bottom: 15px;
    position: relative;
    .morebtn {
      position: absolute;
      right: 0;
      top: 0;
      line-height: 20px;
      color: #0099ff;
      font-size: 24px;
      width: 30px;
      text-align: right;
    }
  }
  .list {
    padding-right: 60px;
    li {
      display: flex;
      justify-content: space-between;
      margin-bottom: 10px;
      cursor: pointer;
      padding: 5px 10px;
      .txts {
        display: flex;
        width: 70%;
        p {
          color: #333;
          font-size: 12px;
          margin-right: 15px;
          line-height: 20px;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
        }
        p + p {
          border-left: 1px solid #ccc;
          padding-left: 15px;
        }
        .t1 {
          flex: 1;
        }
        .t2 {
          width: 20%;
          flex-shrink: 0;
        }
        .t3 {
          width: 25%;
          flex-shrink: 0;
        }
      }
      .btns {
        opacity: 0;
        visibility: hidden;
        width: 110px;
        flex-shrink: 0;
        .el-button {
          border: 0;
          background: transparent;
          color: #0099ff;
          padding: 0;
        }
      }
      &:hover {
        background: #eee;
        .btns {
          opacity: 1;
          visibility: visible;
        }
      }
    }
  }
}
.sigle-ww {
  width: 444px;
}
.flexbox .flexitem + .flexitem {
  margin-left: 40px;
}
</style>



